@use 'base/variables' as v;
@use 'base/mixins' as m;

.horario {
	background-image: linear-gradient(to bottom, v.$primario 0%, v.$primario 50%, transparent 50%), url(../img/bg_horarios.jpg);
	background-repeat: no-repeat;
	background-position: center, -30rem 50rem;
	background-size: cover;
	color: v.$blanco;

	@include m.telefonoXL {
		background-position: center, -20rem 50rem;
	}

	@include m.tablet {
		background-image: linear-gradient(to right, v.$primario 0%, v.$primario 50%, transparent 50%), url(../img/bg_horarios.jpg);
		background-position: center, 5rem 0rem;
	}

	@include m.desktop {
		background-position: center, 25rem 0rem;
	}

	@include m.desktopXL {
		background-position: center, top right;
		background-size: auto;
	}

	&__grid {
		display: grid;
		grid-template-rows: repeat(2, 1fr);

		@include m.tablet {
			grid-template-rows: unset;
			@include m.grid(2, 5rem);
		}
	}

	&__contenido {
		padding: 5rem 0;
	}

	&__heading {
		@include m.heading;
	}

	&__texto {
		text-align: center;
		font-size: 2.2rem;
		line-height: 1.8;
	}
}

.tabla {
	width: 100%;
	border: .5rem solid v.$secundario;
	text-align: center;
	border-collapse: collapse;
	
	&__thead {
		background-color: v.$secundario;
	}

	&__th {
		padding: 2rem;
	}

	&__tr {
		&:hover {
			background-color: darken(v.$primario, 10%);
			cursor: default;
		}
	}

	&__td {
		padding: 1rem;
		font-size: 2.2rem;
	}
}